.navbar {
  padding: 5px 30px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: relative;
  width: 100%;
  height: 50px;
  background-color: #006400;
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.navbar div {
  display: inline-block;
  position: absolute;
  right: 30px;
  top: 10px;
  color: white;
  font-size: 20px;
  text-transform: uppercase;
}

.navbar div span {
  font-weight: bold;
  font-size: 25px;
}

#linkback a {
  position: absolute;
  color: white;
  text-decoration: none;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  top: 25px;
  font-size: 14px;
}

#logo {
  position: relative;
  height: 100%;
}



